
import SelectComp from './SelectComp.vue';
import { computed, defineComponent, ref } from 'vue';

export default defineComponent({
    components: {
        SelectComp
    },
    setup() {
        const inpPrice = ref(1);
        const inpQty = ref(1);
        const typeOption = ref(false);

        const inpPriceFormat = computed(() => {
            return Number(inpPrice.value)+'.00';
        });

        const increaseValue = (type:string) => {
            if(type === "price"){
                inpPrice.value++;
            } else {
                inpQty.value++;
            }
        };

        const decreaseValue = (type:string) => {
            if(type === "price"){
                if (inpPrice.value > 1) {
                    inpPrice.value--;
                }
            } else {
                if (inpQty.value > 1) {
                    inpQty.value--;
                }
            }
        };

        return {
            inpPrice,
            inpPriceFormat,
            inpQty,
            typeOption,
            increaseValue,
            decreaseValue
        }
    }
});
