
import { defineComponent } from 'vue';
import SelectType from './SelectType.vue';

export default defineComponent({
    components: {
        SelectType
    },
    props: {
        visible: {
            type: Boolean,
            default() {
                return false;
            }
        },
    },
    emits: ['close-this'],
    setup(props,{emit}) {
        const closeNow = () => {
            emit('close-this');
        };

        return {
            closeNow
        }
    }
})
