
import { defineComponent } from 'vue';
import orderType from "@/data/orderType";

export default defineComponent({
    emits: ['close-select'],
    setup(props, {emit}) {
        const OrderOptions = orderType;
        const CloseThis = () => {
            emit('close-select');
        }
        
        return {
            CloseThis,
            OrderOptions,
        }
    }
})
